@import "./style/_color";
@import "./style/_typography";

section {
    display: flex;
    min-width: 100vw;
    min-height: 100vh; }

.hero-left {
    display: flex;
    flex: 1;
    min-width: 40vw;
    min-height: 100vh;
    background-color: $theme-color; }

.hero-right {
    min-width: 60vw;
    height: 100vh;
    overflow: scroll;
    position: sticky;
    top: 0;
    background-color: $theme-color; }
