@import "typography";
@import "color";

a {
    font-weight: 600;
    text-decoration: none;
    &:active {
        opacity: 0.9; } }

a.icon-right {
    &:after {
        font-family: "Font Awesome 5 Free";
        content: "\f105";
        display: inline-flex;
        align-items: center;
        font-weight: 900;
        position: relative;
        left: 8px;
        transition: all .3s ease-in-out; }
    &:hover {
        &:after {
            transform: scale(1.1);
            left: 12px; } } }

a.button-link {
    &:after {
        display: none; } }

a.text-24 {
    &:after {
        left: 8px; } }


a.text-20 {
    &:after {
        left: 8px; }
    &:hover {
        &:after {
            left: 16px; } } }

a.text-16 {
    &:after {
        left: 8px; }
    &:hover {
        &:after {
            left: 8px; } } }

a.text-14 {
    &:after {
        left: 8px; }
    &:hover {
        &:after {
            left: 8px; } } }

a.disabled {
    color: $white-disabled;
    pointer-events: none;
    &:hover {
        cursor: not-allowed; }
    &:after {
        display: none; } }
